@mixin breakpoint($point) {
    @if $point ==lg {
        @media (max-width: 992px) {
            @content;
        }
    }

    @else if $point ==md {
        @media (max-width: 768px) {
            @content;
        }
    }

    @else if $point ==sm {
        @media (max-width: 576px) {
            @content;
        }
    }

    @else if $point ==xs {
        @media (max-width: 370px) {
            @content;
        }
    }

    @else if $point ==xl {
        @media (max-width: 1200px) {
            @content;
        }
    }

    @else if $point ==xxl {
        @media (max-width: 1400px) {
            @content;
        }
    }
}

@import '../../../global.css';



.container {
    color: white;
    margin-top: -150px;
    padding-top: 250px;
    background-color: var(--light);


}

.chat {
    font-size: 25px;
    max-width: 50vw;
    margin: 0 auto;

    @include breakpoint (xl) {
        max-width: 65vw;
    }

    @include breakpoint (lg) {
        max-width: 80vw;
    }

    @include breakpoint (md) {
        max-width: 100vw;
        font-size: 20px;
        font-family: "GTEesti-light";
    }
}